import React from 'react';
import { useBarcode } from 'react-barcodes';

const Barcode = (props) => {
    const {inputRef} = useBarcode({
        value: props.code,
        width: 1
    })
    return ( 
        <img style={{
            width: "120px"
        }} className='barcode' ref={inputRef} />
     );
}
 
export default Barcode;