import React, { useState } from 'react';
import './config.css'
// import ReactToPrint from 'react-to-print';


const Config = (props) => {
    const {c} = props
    const [file, setFile] = useState(null)
    const renderPrint = () => {
        return <div id='print_section'>
            {/* <input onClick={() => c(file)} id='print_button' type='button' value='IMPRESSION' /> */}
            <input onClick={() => setFile(null)} type='button' value='changer de fichier' />
        </div>
    }
    return ( 
        <div>
            <h1>Configuration de mon impression</h1>
            {!file ? <input onChange={(e) => c(e.target.files[0])} type='file' id='files' accept='.csv' required /> : renderPrint()}
           
        </div>
     );
}
 
export default Config;