

import React, { Component } from 'react'
import Barcode from './Barcode'

export default class Print extends Component {
    constructor(props) {
        super(props)
        this.state = {
            data: props.file
        }
    }
    render() {
        var data = this.props.file
        console.log(this.state)
        var printed = 0
        var labelWidth = 4;
        var labelHeight = 3;
        var maxRows = Math.floor(29.7 / labelHeight) - 1
        var maxCols = Math.floor(21 / labelWidth) - 1
        var maxLabelsToPrint = maxRows * maxCols
        console.log(21 / labelWidth)
        console.log(maxRows)
        console.log(maxCols)
        var fromatedW = labelWidth.toString() + "cm"
        var formatedH = labelHeight.toString() + "cm"
        console.log(fromatedW)

        const style = {
            document: {
                border: "1px solid black",
                paddingTop: "5px",
                width: "210mm",
                height: "297mm",
                margin: "auto",
                display: "flex",
                flexWrap: "wrap",
                alignContent: "flex-start",
                justifyContent: "center",
            },
            label: {
                width: fromatedW,
                height: formatedH
            },
            textRow: {
                padding: "0px"
            }
        }
        return (
            <div style={style.document} className="document">
                {this.state.data.map((el, i) => {
                    if (printed === maxLabelsToPrint) {
                        console.log("LIMIT",data)
                        return
                    }
                    if (el.QUANTITEE_RECUE === "0") return
                    if (el.FOURNISSEUR === "") return
                    console.log("NEW LABEL",data)
                    const multiples = []
                    const quantity = parseInt(el.QUANTITEE_RECUE)
                    for (let index = 0; index < quantity; index++) {
                        printed++

                        multiples.push(<div key={i + index} style={style.label} className='item'>
                            <div style={style.textRow} id='item_row_first'>
                                <p>{el.FORMES}</p>
                                <p>{el.TAILLE}</p>
                            </div>
                            <div id='item_row_second'>
                                <p>{el.COULEUR}</p>
                                <p>{el.PV + "€"}</p>
                            </div>
                            <div id='item_row_third'>
                                <p>{el.FOURNISSEUR}</p>
                                <p>{el.REFERENCE}</p>
                            </div>

                            <div>
                                <Barcode code={el.CODES_BARRES} />
                            </div>
                        </div>)
                    }
                    const newEl = { ...el, QUANTITEE_RECUE: "0" }
                    data = [...this.state.data, newEl]
                    return multiples

                })}
            </div>
        )
    }
}
